.carousel {
  //border: 1px solid #6C7151;
  padding: 10px 20px 20px 20px;
  margin-top: 100px;
  margin-bottom: 200px;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    border: 1px solid #6C7151;
  }
  &:after {
    content: "";
    display: block;
    width: 150px;
    height: 80px;
    position: absolute;
    right: 0;
    top: 0;
    border-bottom: 1px solid #6C7151;
    border-left: 1px solid #6C7151;
    background: #fff;
    z-index: 1;
    @media (max-width: $md-max) {
      display: none;
    }
  }
  .swiper-pagination {
    display: none;
    position: relative;
    @media (max-width: $md-max) {
      margin-top: 20px;
      display: block;
    }
    .swiper-pagination-bullet {
      background: #6C7151;
      border-radius: 0;
      width: 100%;
      max-width: 30px;
      height: 4px;
    }
  }
}

.carousel_heading {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 20px 0;
  @media (max-width: $md-max) {
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
}

.carousel_title {
  font-size: 32px;
}

.carousel_qty {
  color: #999F74;
  font-size: 20px;
  margin-left: 30px;
  @media (max-width: $md-max) {
    margin-left: 0;
    margin-top: 15px;
    font-size: 17px;
    line-height: 21px;
  }
}

.carousel_navigation {
  position: absolute;
  right: 23px;
  top: -1px;
  width: 100px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  @media (max-width: $md-max) {
    display: none;
  }
  .swiper-button {
    //position: relative;
    color: #6C7151;
  }
}

.product_image {
  position: relative;
}

.product_sticker {
  background: #f3ea92;
  height: 35px;
  width: fit-content;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  color: #4E523D;
  position: absolute;
  left: 5px;
  top: 5px;
  &:after {
    content: "";

    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-left-width: 0;
    border-right-width: 15px;
    border-top: 35px solid #f3ea92;

    position: absolute;
    left: 100%;
    top: 0;
    bottom: 0;
  }
}

.product_info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 0 20px;
  @media (max-width: $md-max) {
    padding: 0 5px;
  }
}

.product_number {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #6C7151;
  font-size: 12px;
  img {
    margin-right: 10px;
  }
}

.product_rating {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 12px;
  &:hover {
    text-decoration: underline;
  }
  img {
    margin-right: 10px;
  }
}

.product_title {
  font-size: 16px;
  line-height: 24px;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3; // количество строк
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  height: 72px;
  padding: 0 20px;
  &:hover {
    text-decoration: underline;
  }
  @media (max-width: $md-max) {
    padding: 0 5px;
  }
}

.product_status {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  color: #17856B;
  font-size: 12px;
  margin-bottom: 15px;
  padding: 0 20px;
  @media (max-width: $md-max) {
    padding: 0 5px;
  }
}

.product_price-line {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  padding: 0 20px;
  @media (max-width: $md-max) {
    padding: 0 5px;
  }
}

.product_price-main {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  margin-right: auto;
}

.product_price-sale {
  background: #F2E886;
  font-size: 12px;
  line-height: 16px;
  color: #000000;
  padding: 0 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 20px;
}

.product_price-old {
  color: #6C7151;
  font-size: 16px;
  margin-left: 5px;
  text-decoration: line-through;
}

.product_buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  @media (max-width: $md-max) {
    padding: 0 5px;
  }
}

.btn {
  background: none;
  outline: none;
  box-shadow: none;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  cursor: pointer;
}

.btn-buy {
  background: transparent;
  border: 1px solid #96A091;
  color: #4E523D;
  transition: 0.3s;
  width: 100%;
  img {
    margin-right: 10px;
  }
  &:hover {
    background: #96A091;
  }
}

.btn-gray {
  min-width: 50px;
  background: #F9F9F9;
  border: 1px solid #F9F9F9;
  transition: 0.3s;
  margin-left: 5px;
  &:hover {
    background: transparent;
    border: 1px solid #96A091;
  }
}