.card {
  border: 1px solid #6C7151;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  height: 100%;
  position: relative;
  padding: 20px;
  overflow: hidden;
  @media (max-width: $lg-max) {
    padding: 20px 10px;
  }
  @media (max-width: $md-max) {
    padding: 15px;
    margin-top: 20px;
  }
  &.card_vertical {
    height: 490px;
    @media (max-width: $lg-max) {
      height: 450px;
    }
    @media (max-width: $md-max) {
      height: 200px;
    }
    @media (max-width: $xs-max) {
      height: 180px;
    }
  }
  &.card_horizontal {
    height: 225px;
    @media (max-width: $lg-max) {
      height: 215px;
    }
    @media (max-width: $md-max) {
      height: 200px;
    }
    @media (max-width: $xs-max) {
      height: 180px;
    }
    &:not(:first-of-type) {
      margin-top: 40px;
      @media (max-width: $lg-max) {
        margin-top: 20px;
      }
    }
  }
  & * {
    z-index: 1;
  }
  &:hover {
    .card_image {
      transform: scale(1.07);
    }
  }
}

.card_column {
  width: 100%;
  margin-left: 40px;
  @media (max-width: $lg-max) {
    margin-left: 20px;
  }
  @media (max-width: $md-max) {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }
}

.card_image {
  position: absolute;
  right: 0;
  bottom: 0;
  object-fit: cover;
  z-index: 0;
  height: 100%;
  transition: 0.3s;
}

.card_title {
  font-size: 28px;
  line-height: 36px;
  @media (max-width: $lg-max) {
    font-size: 24px;
    line-height: 30px;
  }
  @media (max-width: $md-max) {
    margin-bottom: 5px;
  }
  @media (max-width: $xs-max) {
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 5px;
  }
}

.card_link {
  display: flex;
  align-items: center;
  color: $color-yellow;
  font-size: 16px;
  img {
    margin-left: 10px;
  }
}