@import './home/index';

.top-hero {
  background: center / contain no-repeat url("../img/bg-spots.png") #4E523D;
  color: $color-white;
  padding: 100px 0;
  @media (max-width: $md-max) {
    padding: 85px 0;
  }
  @media (max-width: $xs-max) {
    padding: 70px 0;
  }
}

.top-hero_inner {
  display: flex;
  width: 100%;
  @media (max-width: $md-max) {
    flex-direction: column;
  }
}

.top-hero_cards {
  display: flex;
  width: 100%;
  position: relative;
  z-index: 2;
  @media (max-width: $md-max) {
    flex-direction: column;
  }
}

.top-hero_title {
  max-width: 290px;
  width: 100%;
  height: fit-content;
  position: relative;
  @media (max-width: $md-max) {
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 115px;
    max-width: 500px;
  }
  @media (max-width: $xs-max) {
    max-width: 100%;
  }
  & * {
    position: relative;
    z-index: 2;
  }
}

.top-hero_h1 {
  font-size: 44px;
  line-height: 60px;
  @media (max-width: $lg-max) {
    font-size: 40px;
    line-height: 52px;
  }
  @media (max-width: $md-max) {
    font-size: 32px;
    line-height: 44px;
  }
  @media (max-width: $xs-max) {
    font-size: 26px;
    line-height: 36px;
  }
}

.top-hero_link {
  display: block;
  font-size: 28px;
  line-height: 36px;
  color: $color-yellow;
  margin-top: 40px;
  @media (max-width: $xs-max) {
    font-size: 24px;
    line-height: 30px;
  }
  @media (max-width: $xs-max) {
    font-size: 20px;
    line-height: 24px;
  }
}

.top-hero_triangle {
  margin-top: -140px;
  max-width: 324px;
  position: absolute;
  z-index: 1;
  top: calc(100% - 40px);
  left: 50%;
  transform: translateX(-50%);
  @media (max-width: $md-max) {
    margin-top: -130px;
  }
  @media (max-width: $xs-max) {
    margin-top: -120px;
  }
}


